import React, { Component } from 'react'
import { useState, useEffect } from 'react'
import axios from 'axios';
import {Link, Routes, Route, useNavigate} from 'react-router-dom';


function ShowAllVc() {

    const [allvcdetails, setAllvcdetails] = useState([])
    const navigate = useNavigate();  
    useEffect(() => {
      axios.get('http://43.204.198.28/reactProject/getAllVcDetails.php')
        .then(res => {
            console.log(res.data);
            setAllvcdetails(res.data);
          })
      },[]);
  

    return (
    <div > 
    dgf<br />
    dgf<br />
    dgf<br />
    dgf<br />
    dgf<br />
    dgf<br />
<h2 style={{color:'black'}}> VC Details</h2>
<button >Export to Excel</button>
<button >Export to Pdf</button>
<button >Search</button>
<button onClick={() => navigate(-1)} style={{float:'right'}}>Back to Home Page</button>
<br />

    <table className = "table table-striped table-bordered">
    <thead>
        <tr>
            <th> VC Date</th>
            <th> VC Time</th>
            <th> VC Mode</th>
            <th> VC ID</th>
            <th>Chaired By</th>
            <th> Description</th>
        </tr>
    </thead>
    <tbody>
    {
                                    allvcdetails.map((a) => 
                                        <tr key = {a.VC_Mode}>
                                             <td> {a.VC_Date} </td>   
                                             <td> {a.VC_STime}</td>
                                             <td> {a.VC_Mode}</td>
                                             <td> {a.VC_Id}</td>
                                             <td> {a.VC_CName}</td>
                                             <td> {a.Description} </td>
                                        </tr>
                                    )
                                }       
    </tbody>
</table>




    </div>
  )
}

export default ShowAllVc