import React, {useState, useEffect} from 'react' ;
import axios from 'axios';
import { useNavigate} from 'react-router-dom';


export default function Login() {

  let navigate = useNavigate();
  const [enteredUser , setEnteredUser] = useState('');
  const [enteredPwd , setEnteredPwd] = useState('');
  const [isFormValid, setFormIsValid] = useState(false);
  const [isLoggedIn,setIsLoggedIn] =useState(false);
  useEffect(() => {
        setFormIsValid(
            enteredUser.includes('@') && enteredPwd.trim().length>6  );
    }, [enteredUser,enteredPwd]);


  const userChangeHandler =(e) =>{

    setEnteredUser(e.target.value);
 
  }
   const pwdChangeHandler =(e) =>{

    setEnteredPwd(e.target.value);

  }

       
      const handleSubmitClick = (e) => {
        e.preventDefault();   
        if(enteredUser.trim().length===0 || enteredPwd.trim().length===0)
        {return;}
        setEnteredUser('');
        setEnteredPwd('');
        
              axios.post('http://43.204.198.28/APIforReact/loginsubmitbackup.php',{user : enteredUser, pwd: enteredPwd})
                    .then((res) => {
                        if(res.data.success===1)
                        {
                            console.log(res.data.success);
                            localStorage.setItem('isLoggedIn','true');
                            setIsLoggedIn(true);
                              navigate('/Home');
                        }
      //                  else{}        
//                    }).catch((error) => {
  //                      console.log(error)
                   });
  
      }
 
      return (
    <div id="wrapper">
    <section id="login" className="login">
		<div className="login-content">
        <div className="container">
        <div className="row">
            <div className="col-lg-3 col-md-2"></div>
            <div className="col-lg-6 col-md-8 login-box">
                <div className="col-lg-12 login-key">
                    <i className="fa fa-key" aria-hidden="true"></i>
                </div>
                <div className="col-lg-12 login-title">
                    ADMIN PANEL
                </div>

                <div className="col-lg-12 login-form">
                    <div className="col-lg-12 login-form">
  
                        <form onSubmit={handleSubmitClick}>

		                    <div className="form-group">
                                <label className="form-control-label">USERNAME</label>
                                <input type="text" name="username" 
                                className="form-control" 
                                 value={enteredUser}
                                onChange={userChangeHandler}/>
                            </div>
                            <div className="form-group">
                                <label className="form-control-label">PASSWORD</label>
                                <input type="password" name="password" 
                                className="form-control" 
                                value={enteredPwd}
                                onChange={pwdChangeHandler}/>
                            </div>

                            <div className="col-lg-12 loginbttm">
                                <div className="col-lg-6 login-btm login-text">
                                </div>
                                <div className="col-lg-6 login-btm login-button">
                                    <button type="submit" name="submit" className="btn btn-outline-primary">LOGIN</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
           </div>
</div>

		</div>		
    </section>

</div>
)

}  
